@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@400;700);
.ManualInput .inputs{display:grid;grid-template-columns:1fr 1fr;margin:18px 0;grid-column-gap:18px;-webkit-column-gap:18px;column-gap:18px}.ManualInput .action{margin-bottom:18px;text-align:center}.ManualInput .action .compare{display:inline-block;margin:auto;padding:6px 12px;border:1px solid #fff;background-color:#021319;color:#fff;cursor:pointer}.SingleInput textarea{width:100%;height:400px;font-family:Inconsolata,monospace}
.DisplayError{margin:18px 0;padding:6px 12px;border:1px solid #790b25;background-color:#ef476f}.DisplayError pre{color:#021319}.ComparedValue .collapsable{cursor:pointer}.ComparedValue .collapsable:hover{background-color:#052c39}.ComparedValue .collapsable.mixed:hover{background-color:#c86a00}.ComparedValue.left{background-color:#790b25}.ComparedValue.left .collapsable:hover{background-color:#bf113a}.ComparedValue.right{background-color:#035a43}.ComparedValue.right .collapsable:hover{background-color:#048c68}.ComparedValue .mixed{background-color:#954f00}.ComparedValues pre{margin:0;padding:3px;font-family:Inconsolata,monospace}
.App{margin:18px}.App h1{margin:18px;text-align:center}
*,::before,::after{box-sizing:border-box;font-family:Roboto,Arial,sans-serif}body{margin:0;background-color:#021319;color:#fff;font-family:Roboto,Arial,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}
